<template>
	<div class="personal">
		<div class="personal_head">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p class="personal_title">个人中心</p>
		</div>
		<div class="personal_banner">
			<img src="../static/img/icon_header.png" alt="" class="icon_header"/>
			<p class="username">{{ personalInfo.userName || '乐友' }}</p>
			<div class="personal_banner02">
				<p class="personal_banner02_text">
					<i class="icon_currency"></i>
					<span>我的积分:</span>
				</p>
				<p class="personal_banner02_number">{{ personalInfo.amount }}</p>
				<img src="../static/img/img_jfmx_btn.png" class="img_jfmx_btn" @click="toPath('/incomeDetails')"/>
			</div>
		</div>
		<ul class="personal_Btn_List">
			<li v-for="item in personalBtnList" :key="item.id" @click="toPath(item.path)">
				<img :src="item.imgUrl" alt="" class="imgUrl" />
				<div class="personal_Btn_List_box">
					<img :src="item.textUrl" alt="" class="textUrl" />
					<p class="text" :style="'color:'+item.color">{{ item.text }}</p>
				</div>
				<img :src="item.arrowUrl" class="arrowUrl"/>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		name:'personal',
		data(){
			return{
				personalInfo:{
					userId:'乐友',
					amount:0
				},
				personalBtnList:[
					{
						id:0,
						imgUrl:require('../static/img/icon_personal_list01.png'),
						textUrl:require('../static/img/icon_personal_text01.png'),
						arrowUrl:require('../static/img/icon_personal_arrow01.png'),
						text:'海量好物等你来免费兑换',
						path:'/pointsMall',
						color:'#ff8aae'
					},
					{
						id:1,
						imgUrl:require('../static/img/icon_personal_list02.png'),
						textUrl:require('../static/img/icon_personal_text02.png'),
						arrowUrl:require('../static/img/icon_personal_arrow02.png'),
						text:'看看自己的运气怎么样吧',
						path:'/freeAccess/participate',
						color:'#e094ff'
					},
					{
						id:2,
						imgUrl:require('../static/img/icon_personal_list03.png'),
						textUrl:require('../static/img/icon_personal_text03.png'),
						arrowUrl:require('../static/img/icon_personal_arrow03.png'),
						text:'透明 公开 公平 公正',
						path:'/freeAccess/winningPublicity',
						color:'#ffb541'
					},
					{
						id:3,
						imgUrl:require('../static/img/icon_personal_list04.png'),
						textUrl:require('../static/img/icon_personal_text04.png'),
						arrowUrl:require('../static/img/icon_personal_arrow04.png'),
						text:'地址准确才能收到好物哦',
						path:'/myaddress',
						color:'#81dd74'
					},
					{
						id:4,
						imgUrl:require('../static/img/icon_personal_list05.png'),
						textUrl:require('../static/img/icon_personal_text05.png'),
						arrowUrl:require('../static/img/icon_personal_arrow05.png'),
						text:'专人在线解答使用问题',
						path:'/customerService',
						color:'#72c2ff'
					}
				]
			}
		},
		mounted(){
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			_this.getUserInfo();
		},
		methods:{
			goBack(){
			  this.$router.go(-1);
			},
			toPath(path){
				var _this = this;
				_this.$router.push({path:path});
			},
			getUserInfo(){
				let _this = this;
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj ));
				_this.$axios({
				  method: 'post',
				  url: '/getUserInfo',
				  data:data,
				}).then(function(res) {
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					_this.personalInfo = reData;
				  }
				});
			}
		}
	}
</script>

