	}
	.personal_banner02{
		width: 100%;
		height: 4.19rem;
		background: url(../static/img/bg_personal_banner02.png) no-repeat;
		background-size: 100%;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		box-sizing: border-box;
		padding:0.9rem 1.15rem 0 1.15rem;
